/*=======================================================
# パンくずリストモジュール
=======================================================*/
.bl_breadcrumb {
  display: flex;
  overflow-x: auto;

  &_inner {
    display: flex;
    margin-right: auto;
    margin-left: auto;

    & > *:last-child {
      margin-right: 0;
    }

    & > li {
      margin-right: 15px;
    }
  }

  &_link {
    display: flex;
    justify-content: center;
    

     // ＞２つめ以降はマークをつける
     & + &::before {
      content: ">";
      margin-right: 10px;
    }
  }

}