@charset "UTF-8";
/*=======================================================
# パンくずリストモジュール
=======================================================*/
.bl_breadcrumb {
  display: flex;
  overflow-x: auto;
}

.bl_breadcrumb_inner {
  display: flex;
  margin-right: auto;
  margin-left: auto;
}

.bl_breadcrumb_inner > *:last-child {
  margin-right: 0;
}

.bl_breadcrumb_inner > li {
  margin-right: 15px;
}

.bl_breadcrumb_link {
  display: flex;
  justify-content: center;
}

.bl_breadcrumb_link + .bl_breadcrumb_link::before {
  content: ">";
  margin-right: 10px;
}
